@import '../../../base';

.form-landing-splash-container {
  margin: 20% 0 5% 0;
  padding-right: 92px;
  .mas-container {
    display: flex;
    align-items: center;

    margin: 50px 0 20px 0;

    .h7 {
      position: relative;
      z-index: 1;
      max-width: 150px;
      margin-left: 10px;
      @media screen and (max-width: 1110px) {
        max-width: 120px;
      }
    }

    img {
      width: 100px;
      height: auto;

      filter: saturate(0%);
    }
  }

  h1 {
    position: relative;
    z-index: 1;
    max-width: 300px;
  }

  .splash-img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 256px;
  }
}

.form-landing-insurer-carousel {
  .insurer-logo-carousel {
    margin-top: 36px;
    .insurer-logo-sq {
      height: 60px;

      img {
        object-fit: contain;
        width: 100px;
        height: 60px;
      }
    }
  }

  .owl-prev,
  .owl-next {
    position: absolute;
    top: 0 !important;
    color: transparent;
    width: 60px;
    height: 80px;

    &:before {
      border: 0 !important;
    }

    &:hover {
      filter: invert(0.7) saturate(0);
    }
    
    transition: filter 0.2s ease;
    background-image: url('../../../assets/images/FormLanding/CarouselArrow.svg');
    background-repeat: no-repeat;
    background-position: top center;
  }

  .owl-prev {
    left: 0 !important;
    transform: translateX(-60px) translateY(-5px) !important;
    background-position: top center;

    &:active {
      filter: invert(0.7) saturate(0);
      transform: translateX(-60px) translateY(-5px) scale(1.1) !important;
    }
  }

  .owl-next {
    right: 0 !important;
    transform:  translateX(60px) translateY(-5px) scaleX(-1) !important;
    background-position: top center;

    &:active {
      filter: invert(0.7) saturate(0);
      transform: translateX(60px) translateY(-20px) scale(-1.1) !important;
    }
  }
}