.travel-container {
  position: relative;
  .promo-outer-extra {
    margin-top: -40px;

    @media screen and (max-width: 576px) {
      margin-top: -60px !important;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 420px) {
      margin-top: -80px !important;
      .desc {
        max-width: 100%;
        margin-top: -180px !important;
      }
    }
  }

  .promo-img-extra {
    @media screen and (max-width: 576px) {
      margin-top: -50px;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 420px) {
      margin-top: 50px;
      .desc {
        max-width: 100%;
      }
    }
  }

  .button {
    margin-top: 16px;
    @media screen and (min-width: 576px) {
      margin-top: 0;
      transform: translateY(-10px);
      padding-left: 10px;
    }
  }

  .text-sel-col {
    padding: 0;
    &:nth-of-type(1) {
      padding-right: 5px;
    }

    &:nth-of-type(2) {
      padding-left: 5px;
    }
  }

  .travel-top-info {
    @media screen and (min-width: 992px) {
      h3 {
        min-height: 0 !important;
      }
    }
  }
}

.travel-detail-container {
  .piggy-desc-container {
    @media screen and (max-width: 480px) {
      p {
        text-align: left;
      }
    }
    
    .main-text {
      font-size: 16px !important;
    }

    .desc-text {
      font-size: 9.5px !important;
    }
  }
}

.travel-checkout-container, {
  .piggy-desc-container {
    @media screen and (max-width: 480px) {
      p {
        text-align: left;
      }
    }
    
    .main-text {
      font-size: 14px !important;
    }

    .desc-text {
      font-size: 8.7px !important;
    }
  }
}

.travel-checkout-container {
  .form-margin-container {
    margin-top: 40px;

    @media screen and (max-width: 991px) {
      margin-top: 15px;
    }
  }
}