@import '../../../base';

.form-landing-footer-container {
  background-image: url('../../../assets/images/FormLanding/Footer.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  position: relative;
  z-index: 1;

  &.sticky-padding {
    padding-bottom: 100px;
  }

  .small-container {
    h3 {
      margin-bottom: 10px;
    }
    h6 {
      line-height: normal !important;
    }
  }

  .footer {
    max-width: 1110px;
    padding: 120px 40px 40px 40px;
    margin: auto;
    display: flex;
    align-items: center;

    @media screen and (max-width: 576px) {
      padding: 120px 15px 40px 15px;
    }

    h3,
    h6 {
      color: white;
      font-weight: $font-weight-normal;
    }

    h5 {
      color: white;
      font-weight: $font-weight-bold;
      font-size: $font-size-small;

      @media screen and (max-width: 767px) {
        margin-top: 20px;
        padding-left: 0px !important;
        text-align: left;
      }
    }
    h6 {
      line-height: 11px;
    }
    .content-label {
      color: white;
      font-weight: $font-weight-semibold;
      font-size: $font-size-smallest;

      @media screen and (max-width: 767px) {
        text-align: left;
      }
    }
  }

  .big-footer {
    padding: 240px 15px 40px 15px;

    &.full {
      padding-top: 280px;
    }

    @media screen and (min-width: 576px) {
      &.full {
        padding-top: 380px;
      }
    }

    @media screen and (min-width: 768px) {
      padding: 240px 40px 40px 40px;

      &.full {
        padding: 280px 80px 80px 80px;
      }
    }
  }

  .pre-line {
    white-space: pre-line;
  }

  .contact-info {
    max-width: 200px;
  }

  .social-media-group {
    margin-left: 10px;
    margin-top: 25px;
    margin-bottom: 20px;
    max-width: 180px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;

    .social-media-icons {
      width: 30px;
      height: 30px;
    }
  }

  .facebook-like {
    margin-left: 10px;
  }

  .download-now {
    margin-top: 20px;
    padding: 0px 10px;
  }
  .download-links {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .download-img {
    width: 85%;
    height: auto;
  }

  .col-display {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .content-item {
      padding-left: 0px;
    }
  }

  .copyright {
    display: flex;
    justify-content: center;
    padding: 30px 0px !important;

    h6 {
      font-size: $font-size-small;
      font-weight: $font-weight-bold;
    }
  }

  h5.products-header {
    padding-left: 15px !important;
  }

  .copyright-mobile {
    margin-top: 20px;
    padding-left: 10px;

    h6 {
      font-size: $font-size-smallest;
    }
  }

  .link {
    width: 100%;
    text-align: left;
  }

  @media screen and (max-width: 767px) {
    .contact-info,
    .social-media-group {
      margin-left: auto;
      margin-right: auto;
    }

    .facebook-like {
      margin-left: 0px;
      justify-content: center;
    }
    .download-now {
      margin-top: 0px;
    }
  }

  @media screen and (min-width: 768px) {
    .copyright {
      justify-content: flex-end;
    }
    .col-display {
      flex-direction: row;
      align-items: flex-start;
    }
  }
}

.form-landing-footer-policypal {
  position: absolute;
  left: 0;
  bottom: 5%;

  img {
    transform: translateY(calc(-50px + (1 * 1vw)));

    @media screen and (max-width: 1200px) {
      transform: translateY(calc(-120px + (5 * 1vw)));
    }

    @media screen and (max-width: 767px) {
      transform: translateY(calc(-200px + (15 * 1vw)));
    }

    @media screen and (max-width: 600px) {
      transform: translateY(calc(-180px + (20 * 1vw)));
    }

    @media screen and (max-width: 500px) {
      transform: translateY(calc(-160px + (25 * 1vw)));
    }
  }

  .big-footer {
    transform: translateY(calc(-200px + (1 * 1vw)));

    @media screen and (max-width: 1200px) {
      transform: translateY(calc(-200px + (5 * 1vw)));
    }

    @media screen and (max-width: 767px) {
      transform: translateY(calc(-200px + (15 * 1vw)));
    }

    @media screen and (max-width: 500px) {
      transform: translateY(calc(-200px + (25 * 1vw)));
    }
  }


  img {
    width: 100vw;
    height: auto;
  }
}

.form-landing-footer-policypal-full {
  position: absolute;
  top: -50%;
  left: 0;
  right: 0;
  img {
    width: 100%;
    height: auto;
    padding-left: 0 !important;
    padding-right: 0 !important;
    filter: $color-filter-input-form-landing-desc;
  }

  @media screen and (max-width: 767) {
    top: 0;
  }
}